export default Vue.extend({
  name: 'ItemPr',
  props: {
    pr: {type: Object as ()=> PullRequest},
  },
  filters: {
    formatDate: (uglyDate: string)=>moment(uglyDate).format("MMMM d YYYY"),
    githubLink: (user:string)=>`https://github.com/${user}`

  }
});

